'use strict';

const { fsRoot } = require('./dtconfig');
const { fs, Path, Buffer } = require('filer');
const sh = new fs.Shell();

window.fs = fs;
window.path = Path;
window.Buffer = Buffer;


// File Upload
document.addEventListener("DOMContentLoaded",() => {
  document.querySelector('#dt_upload').addEventListener('change', (e) => {
       for (var i=0; i < e.target.files.length; i++) {
         uploadFile(e.target.files[i].name, e.target.files[i]);
       }
      function uploadFile (fileName, file) {
      // Browser FileReader API
       const reader = new FileReader();
       reader.onload = function(e) {
         const resultArr = new Uint8Array(e.target.result);
         const buffer = new Buffer(resultArr);
          // Filer API - write file to Indexed DB
           fs.writeFile('/' + fileName, buffer, function(err) {
             if(err) { 
               alert(`File Upload error`); 
              } 
           });  
       };
       reader.readAsArrayBuffer(file);
       };
  });  
});

// File Download
const readFile = file => {
  const fileName = file.replace(/\//g, '');
  fs.readFile(file,'utf8' , (err, data) => {
    if (err) {
      //console.error(err);
      alert('File not found! Verify path and filename.')
      return;
    }
    setDownload(fileName, data);
    document.querySelector('#dt_downpath').value = '';
  });
  function setDownload(fileName, data) {
    const dt = document.createElement("a");
    dt.download = fileName;
    dt.href = window.URL.createObjectURL(new Blob([data]));
    dt.dataset.downloadurl = "application/octet-stream:" + dt.download + ":" + dt.href;
    dt.click();
  };
}

document.addEventListener("DOMContentLoaded",() => {
  document.querySelector('#dt_downpath').addEventListener('keypress', (e) => {
    if (e.which == 13) {
     readFile(e.target.value);
    }
});
});

// Welcome files placement in filesystem
function welcome() {
  const readme = `Welcome to Digital Twin! You can access mounted files /mnt using upload and download options. This Digital Twin is a compilation of Embedded Linux system, SPRECON specific process, Filer filesystem, and I/O.`;
  fs.writeFile('/README.txt', readme, (err) => {
    if(err) console.error(err);   
  });
};

module.exports = {
  welcome,
  fs,
  sh,
  Path,
  Buffer
};
