'use strict';

const { dtSnapshotsCache, snapshotUrl } = require('./dtconfig');

const getSnapshotUrl = () => new URL(snapshotUrl, window.location);

// check for the last snapshot available.
const getSnapshot = () =>
  caches
    .open(dtSnapshotsCache)
    .then(cache => cache.match(getSnapshotUrl()));

// Check for the digital twin snapshot availability
const hasSnapshot = () =>
  getSnapshot().then(res => !!res);

// Saving the digital twin state as snapshot.
const saveSnapshot = (err, state) => {
  const blob = new Blob([new Uint8Array(state)], {
    type: 'application/octet-stream',
  });
  const res = new Response(blob, {
    status: 200,
    statusText: 'OK, The DT snapshot has been created.',
  });

  const header = new Headers();
  header.append('Content-Type', 'application/octet-stream');
  header.append('Content-Length', blob.size);
  const url = getSnapshotUrl();
  const req = new Request(url, {
    method: 'GET',
    header,
  });

  caches
    .open(dtSnapshotsCache)
    .then(cache => cache.put(req, res))
    .catch(err => console.error(err));
};

module.exports = {
  getSnapshot,
  hasSnapshot,
  saveSnapshot
};