'use strict';

const filesystem = require('./filesystem');
const { V86Starter } = require('v86');
const { emulatorDefaultParams } = require('./dtconfig');
const dtSnapshot = require('./dtsnapshot');
require("regenerator-runtime");

let emulator = null;

const bootDT = async xterm => {
  if (!emulator) {
  console.log('boot called');
  const isSnapshotExists = await dtSnapshot.hasSnapshot();
  if (isSnapshotExists) {
    try {
      await snapshotBoot(xterm);
    } catch(err) {
      console.log('Cached boot failed:', err.message);
      console.log('trying normal boot...please wait')
      await freshBoot(xterm);
    }
  } else {
    console.log('Booting...please wait.')
    await freshBoot(xterm);
  } 
}
else {
  return;
}
};

// Restart the Digital Twin emulator
const suspend = () => {
  if (emulator && emulator.is_running()) {
      emulator.stop();
  }
};

// Set welcome screen and clear booting console message.
const setWelcomeScreen = (emulator, xterm) => {
  xterm.reset();
  xterm.writeln('Now you can interact with the Digital Twin using this serial console.');
  xterm.writeln('Welcome to SPRECON Digital Twin');
  xterm.writeln('The filesystem is mounted in /mnt. Guest <-> Host');
  xterm.write('/ # ');
   xterm.onData((data) => {
    emulator.serial0_send(data)
   });
   emulator.add_listener('serial0-output-char', char => xterm.write(char));
   xterm.clear();
};

// Pass Filer filesystem object
const getEmulatorParams = () => {
  const params = Object.create(emulatorDefaultParams);
  params.filesystem = filesystem;
  return params;
};

// Inital digital twin booting and taking snapshot.
const freshBoot = async xterm => {
  const params = getEmulatorParams();
  emulator = new V86Starter(params);
  await takeSnapshotAfterBoot(emulator, xterm);
  // iframeSet();
  console.log('fresh bboot');
  filesystem.welcome();
  return emulator;
};

// Booting digital twin from the latest snapshot.
const snapshotBoot = async xterm => {
  console.log('cached boot');
  const params = getEmulatorParams();
  return dtSnapshot.getSnapshot()
    .then(res => res.arrayBuffer())
    .then(buffer =>
      URL.createObjectURL(new Blob([buffer], { type: 'application/octet-stream' } )))
    .then(cacheURL => {
      params.initial_state = { cacheURL };
      emulator = new V86Starter(params);
      setWelcomeScreen(emulator,xterm);
    });
};

// Read the console and  the boot message in the console
const checkBootCompletion = async (emulator, xterm) =>
  new Promise(resolve => {
    let screenData = [];
    let serialData = '';
    let row;
 
    function checkScreenData(data) {
      const rows = data[0];
      const columns = data[1];
      const char = data[2];
      if(rows !== row) {
        row = rows;
        xterm.writeln(screenData.join(''));
        screenData = [];
      }
      screenData[columns] = String.fromCharCode(char);
    }

    function checkSerialData(char) {
       serialData += char;
      // check for the root user character in the terminal
      if (serialData.endsWith('/ # ')) {
        emulator.remove_listener('screen-put-char', checkScreenData);
        emulator.remove_listener('serial0-output-char', checkSerialData);
        xterm.clear();

       resolve();
      }
    }
      
    emulator.add_listener('screen-put-char', checkScreenData);
    emulator.add_listener('serial0-output-char', checkSerialData);
  });

const takeSnapshotAfterBoot = async (emulator, xterm) => {
  // check boot completion, set welcome screen and save the snapshot
  console.log('take snapshot');
  await checkBootCompletion(emulator, xterm);
  setWelcomeScreen(emulator, xterm);
  emulator.save_state(dtSnapshot.saveSnapshot);
  console.log('DT snapshot saved to Browser Cache Storage');
};


module.exports = {
  bootDT,
  suspend,
};
